* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Roboto', sans-serif;}

body {
	background-color: #000;
}

header {
	padding: 20px 15px 0px;
}

header h1 {
	color: #FFF;
	font-size: 20px;
	font-weight: 400;
}

main {
	width: 100%;
}

section {
	padding: 15px;
}

.searchbox {
	display: block;
	width: 100%;
	padding: 10px;

	border: none;
	outline: none;
	background: none;

	background-color: #fff;
	border-radius: 2px;

	color: #333;
	font-size: 20px;
	font-weight: 300;

	transition: 0.4s ease-out;

	font-family: 'Roboto'
}

.results {
	display: flex;
	flex-wrap: wrap;
	margin: 0px -15px;
}

.results .result {
	padding: 15px;
	display: flex;
	flex-direction: column;
	cursor: pointer;
}

.results .result img {
	display: block;
	width: 100%;
	max-width: 120px
}

.results .result h3 {
	color: #FFF;
	font-size: 20px;
	font-weight: 600;
	padding: 10px;
	background: #445565;
	flex: 1 1 100%;
	transition: 0.4s ease-out;
}

.results .result:hover h3 {
	background: #FFF;
	color: #223343;
}

.popup {
	position: fixed;
	top: 0;
	right: 0;
	width: 50vw;
	height: 100vh;
	background-color: #fff;

	overflow-y: scroll;
}

.popup .content {
	display: block;
	width: 100%;
	max-width: 960px;
	padding: 25px;
	color: #333;
}

.popup .content h2 {
	font-size: 28px;
	font-weight: 600;
}
.popup .content h2 span {
	font-weight: 300;
	font-size: 20px;
}

.popup .content .rating {
	margin-bottom: 30px;
}

.popup .content .plot {
	display: flex;
	flex-wrap: wrap;
	margin: 0px -15px 30px;
}

.popup .content .plot img {
	flex: 1 1 50%;
	max-width: 300px;
	padding: 0px 15px;
}

.popup .content .plot p {
	flex: 1 1 50%;
	padding: 15px;
}

.popup .content .close {
	display: inline-block;
	padding: 10px 20px;
	font-size: 18px;
	font-weight: 400;

	border: 2px solid #ececec;
	background-color: #fff;
	color:#333;
	border-radius: 8px;
	outline: none;
	appearance: none;
	cursor: pointer;

}
